
import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { Http } from "@angular/http";
import { ProjectsService } from '../../shared/services/ProjectsService';

//import 'script.js'

@Component({
    selector: 'app-projects',
    templateUrl: './projects.component.html',
    styleUrls: ['./projects.component.scss'],
    animations: [routerTransition()]

})


export class ProjectsComponent implements OnInit {
	
	wbs;
	// bar chart
	public myArr = ['him','hers','yours','theirs'];
    public barChartOptions: any = {
        scaleShowVerticalLines: false,
        responsive: true
    };
    public barChartLabels: string[] = ['2006', '2007', '2008', '2009', '2010', '2011', '2012'];
    public barChartType: string = 'bar';
    public barChartLegend: boolean = true;

    public barChartData: any[] = [
        { data: [65, 59, 80, 81, 56, 55, 40], label: 'Test Count' }
    ];
	


	public listcolumns: any[] =  [
  {'wbs': 'abc', 'oic': 'sherry'},
  {'wbs': 'def', 'oic':'df'},
  {'wbs': 'ghi', 'oic':'glen' }];
  
  
	chart1= {
    'id' : 'chart1',
    'width' : 600,
    'height' : 400,
    'type' :'column3d',
    'dataFormat' : 'json'
	}
	
	chart2= {
    'id' : 'chart2',
    'width' : 600,
    'height' : 400,
    'type' :'pie3d',
    'dataFormat' : 'json'
	}
    dataSource;
	dataSource2;
	
	constructor(private _ProjectsService: ProjectsService)
    {
        console.log("Reading _ConfigurationService ");
    	
		
    }
	
	
	public wbsData: any[] = [];
	
    ngOnInit() {
		this._ProjectsService.getWbs()
            .subscribe(
            (res) => {
                this.wbsData = res;
            },
            (error) => console.log("error : " + error),
            () => console.log('Error in GetApplication in Login : ' + Error)
        );
		
		this._ProjectsService.getData1()
            .subscribe(
            (res) => {
                this.dataSource = res;
            },
            (error) => console.log("error : " + error),
            () => console.log('Error in GetApplication in Login : ' + Error)
        );
		
		this._ProjectsService.getData2()
            .subscribe(
            (res) => {
                this.dataSource2 = res;
            },
            (error) => console.log("error : " + error),
            () => console.log('Error in GetApplication in Login : ' + Error)
        );;
		 
	}
}


