import {Injectable} from '@angular/core';
import {Http, Headers, RequestOptions} from '@angular/http';
import {Observable} from "rxjs/Observable";
import { ChartsModule as Ng2Charts } from 'ng2-charts';



import 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';
@Injectable()
export class ProjectsService {


  private wbsUrl = '../assets/data/wbs.json';
  private data1Url = '../assets/data/data1.json';
  private data2Url = '../assets/data/data2.json';

  private headers = new Headers({ 'Accept': 'application/json' });
  private options = new RequestOptions({ headers: this.headers });

 // private project : Project;


 
  constructor(private http:Http) {}

  getWbs() : Observable<any>{
    return this.http.get(this.wbsUrl, this.options)
   //   .map(res => res);
	  .map((res:any) => res.json())
  }
  getData1() : Observable<any>{
    return this.http.get(this.data1Url, this.options)
   //   .map(res => res);
	  .map((res:any) => res.json())
  }
  
  getData2() : Observable<any>{
    return this.http.get(this.data2Url, this.options)
   //   .map(res => res);
	  .map((res:any) => res.json())
  }
}
export class StatModule { }


